import React, { useState, useEffect } from "react";
import { Table, Input, Button, Space, Modal } from "antd";
import emailjs from "@emailjs/browser";
import "antd/dist/reset.css";
import { useAuth0 } from "@auth0/auth0-react";

const { Search } = Input;

function MyTable() {
  const [data, setData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [cart, setCart] = useState([]);
  const [quantity, setQuantity] = useState({});
  const { user, isAuthenticated } = useAuth0();

  useEffect(() => {
    fetch(
      "https://raw.githubusercontent.com/awp250/plumridgenurseries/main/UK%20week%2013%20.json"
    )
      .then((response) => response.json())
      .then((data) => {
        // Increase the price by 60%
        const updatedData = data.map((item) => ({
          ...item,
          Price: item.Price * 1,
        }));
        setData(updatedData);
      });
  }, []);

  const columns = [
    {
      title: "Quantity",
      dataIndex: "Qty",
      key: "Qty",
      sorter: (a, b) => a.Qty - b.Qty,
    },
    {
      title: "Name",
      dataIndex: "Desc",
      key: "Desc",
      sorter: (a, b) => a.Desc - b.Desc,
    },
    {
      title: "Size",
      dataIndex: "Size",
      key: "Size",
    },
    {
      title: "Price",
      dataIndex: "Price",
      key: "Price",
      render: (Price) => `${Price.toFixed(2)}`,
      sorter: (a, b) => a.Price - b.Price,
    },
    {
      title: "Quantity",
      dataIndex: "QtyInCart",
      key: "QtyInCart",
      render: (_, record) => (
        <Space>
          <Input
            type="number"
            min={0}
            value={quantity[record.ID]}
            onChange={(e) =>
              setQuantity({
                ...quantity,
                [record.ID]: parseInt(e.target.value) || 0,
              })
            }
          />
          <Button
            onClick={() => {
              const itemInCart = cart.find((item) => item.ID === record.ID);
              const qtyToAdd = parseInt(quantity[record.ID]) || 0;

              if (qtyToAdd > 0) {
                const updatedQuantity = {
                  ...quantity,
                  [record.ID]: 0,
                };

                if (itemInCart) {
                  itemInCart.QtyInCart += qtyToAdd;
                  setCart([...cart]);
                  localStorage.setItem("cart", JSON.stringify([...cart]));
                } else {
                  const newCartItem = {
                    ...record,
                    QtyInCart: qtyToAdd,
                  };
                  setCart([...cart, newCartItem]);
                  localStorage.setItem(
                    "cart",
                    JSON.stringify([...cart, newCartItem])
                  );
                }

                setQuantity(updatedQuantity);

                const cartItems = JSON.parse(localStorage.getItem("cart"));
                const awp = localStorage.getItem("cart");

                const cartDetails = cartItems.map((item) => (
                  <p key={item.key}>
                    {item.QtyInCart} {item.Desc}
                  </p>
                ));

                const totalPrice =
                  cartItems.reduce((total, item) => {
                    return total + item.Price * item.QtyInCart * 100;
                  }, 0) / 100;

                const totalPriceGBP = `${totalPrice.toFixed(2)}`;

                Modal.info({
                  title: "Order Details",
                  closable: true,
                  content: (
                    <>
                      <div>{cartDetails}</div>
                      <div>Order amount: {totalPriceGBP}</div>
                    </>
                  ),
                  okButtonProps: {
                    disabled: totalPrice < 250,
                  },

                  okText: "Send Order",
                  onOk() {
                    emailjs.send(
                      "service_qjjt1sg",
                      "template_4661q4m",
                      {
                        message:
                          JSON.stringify(JSON.parse(awp), null, 2) +
                          user.name +
                          user.email,
                      },
                      "0sgPYwhrXB1nFVkUz"
                    );
                  },
                });
              }
            }}
          >
            Add to Order
          </Button>
        </Space>
      ),
    },
  ];

  return (
    <>
      <Search
        placeholder="Search"
        value={searchText}
        onChange={(e) => setSearchText(e.target.value)}
        onSearch={(value) => console.log(value)}
        // style={width: fit-content; }
      />

      <Table
        dataSource={data.filter((item) =>
          item.Desc.toLowerCase().includes(searchText.toLowerCase())
        )}
        columns={columns}
        pagination={{ pageSize: 10 }}
        rowKey={(record) => record.ID}
      />
    </>
  );
}

export default MyTable;
