import React from "react";
import { useAuth0 } from "@auth0/auth0-react";

const LogoutButton = () => {
  const { logout, isAuthenticated } = useAuth0();
  return (
    isAuthenticated && (
      <button
        className="btn btn-primary
      mx-5 my-5 px-4 logoutBtn"
        onClick={() => logout()}
      >
        LOGOUT
      </button>
    )
  );
};

export default LogoutButton;
